
import {
  DownOutlined,
  PlusOutlined,
  ArrowLeftOutlined,
  ArrowUpOutlined,
  ArrowDownOutlined
} from '@ant-design/icons-vue'
import { defineComponent, reactive, ref, toRefs } from 'vue'
import { getSectionAndLesson, getCourseById, saveOrUpdateSection, saveOrUpdate } from '@/services/courses'
import InputNumber from '@/components/input-number.vue'
import { Section, Lesson } from '@/types/courses'
import { message } from 'ant-design-vue'

interface S extends Section {
  courseName: string
}

interface L extends Lesson {
  courseName: string;
  sectionName: string
}

const formDate = {
  id: null,
  courseName: '',
  sectionName: '',
  description: '',
  orderNum: null,
  status: 0
}

export default defineComponent({
  name: 'Lesson',
  props: {
    courseId: {
      type: String,
      required: true
    }
  },
  components: {
    DownOutlined,
    InputNumber,
    PlusOutlined,
    ArrowLeftOutlined,
    ArrowUpOutlined,
    ArrowDownOutlined
  },
  setup (props) {
    const state = reactive({
      visible: false,
      show: false,
      sectionForm: ref<S & { courseId: number}>({
        ...formDate,
        courseId: Number(props.courseId)
      }),
      lessonForm: ref<L & { courseId: number }>({
        id: null,
        courseName: '',
        sectionName: '',
        theme: '',
        duration: null,
        isFree: false,
        orderNum: null,
        courseId: Number(props.courseId),
        sectionId: null,
        status: 0
      }),
      sectionEdit: false,
      lessonEdit: false,
      sectionConfirmLoading: false,
      lessonConfirmLoading: false,
      modalVisible: false,
      status: ref<number>(),
      confirmLoading: false,
      loading: false
    })
    const treeData = ref<Section[]>([])
    const course = ref<{
      courseName: string
    }>({
      courseName: ''
    })
    // 获取课程模块课时
    const loadData = async () => {
      const { data } = await getSectionAndLesson(Number(props.courseId))
      if (data.code === '000000') {
        treeData.value = data.data
      }
    }
    const loadCourseById = async () => {
      try {
        const { data } = await getCourseById(Number(props.courseId))
        if (data.code === '000000') {
          course.value.courseName = data.data.courseName
        }
      } catch (error) {}
    }
    // 预览章节
    const viewSection = (item: Section) => {
      state.visible = true
      state.sectionEdit = true
      state.sectionForm = {
        ...item,
        ...course.value
      }
    }
    // 预览课时
    const viewLesson = ($x: Section, $y: Lesson) => {
      state.show = true
      state.lessonEdit = true
      state.lessonForm = {
        ...$y,
        sectionName: $x.sectionName || '',
        ...course.value
      }
    }
    // 状态预览
    const viewStatus = ($x: Section, $y?: Lesson) => {
      if ($y) {
        // 课时
        state.status = $y.status
        state.lessonEdit = true
        state.lessonForm = {
          ...$y,
          sectionName: $x.sectionName,
          ...course.value
        }
      } else {
        // 章节
        state.status = $x.status
        state.sectionEdit = true
        state.sectionForm = {
          ...$x,
          ...course.value
        }
      }
      state.modalVisible = true
    }
    // 更新章节
    const updateSection = async () => {
      state.sectionConfirmLoading = true
      try {
        const { data } = await saveOrUpdateSection(state.sectionForm)
        if (data.code === '000000') {
          state.visible = false
          message.success(data.mesg)
          loadData()
        } else {
          message.error(data.mesg)
        }
      } catch (error) {}
      state.sectionConfirmLoading = false
    }
    // 更新课时
    const updateLesson = async () => {
      state.lessonConfirmLoading = true
      try {
        const { data } = await saveOrUpdate(state.lessonForm)
        if (data.code === '000000') {
          state.show = false
          message.success(data.mesg)
        } else {
          message.error(data.mesg)
        }
      } catch (error) {}
      state.lessonConfirmLoading = false
    }
    const onChange = (val: number) => {
      if (state.sectionEdit) {
        state.sectionForm.status = val
      } else {
        state.lessonForm.status = val
      }
    }
    // 状态更新
    const ok = async () => {
      state.confirmLoading = true
      try {
        if (state.lessonEdit) {
          await updateLesson()
        } else {
          await updateSection()
        }
      } catch (error) {}
      state.confirmLoading = false
      state.modalVisible = false
    }

    const getStatus = (v: number) => {
      switch (v) {
        case 0:
          return '已隐藏'
        case 1:
          return '待更新'
        case 2:
          return '已更新'
        default:
          break
      }
    }
    // 添加章节
    const addSection = () => {
      state.visible = true
      state.sectionEdit = false
      state.sectionForm = {
        courseName: course.value.courseName,
        sectionName: '',
        description: '',
        orderNum: null,
        status: 0,
        courseId: Number(props.courseId)
      }
    }
    // 添加课时
    const addLesson = (section: Section) => {
      state.show = true
      state.lessonEdit = false
      state.lessonForm = {
        courseName: course.value.courseName,
        sectionName: section.sectionName,
        sectionId: section.id,
        theme: '',
        duration: null,
        isFree: false,
        orderNum: null,
        courseId: Number(props.courseId),
        status: 0
      }
    }

    return {
      ...toRefs(state),
      course,
      treeData,
      loadData,
      loadCourseById,
      viewSection,
      viewLesson,
      updateSection,
      updateLesson,
      getStatus,
      viewStatus,
      onChange,
      ok,
      addSection,
      addLesson,
      labelCol: { span: 6 },
      wrapperCol: { span: 16 }
    }
  },
  async created () {
    this.loading = true
    try {
      await this.loadData()
      await this.loadCourseById()
    } catch (error) {}
    this.loading = false
  }
})
